@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

html {
    scroll-behavior: smooth;
}

img {
    max-width: 100%;

}

ul {
    list-style: none;
}

.headerb {
    width: 100%;
    height: 80px;
    position: relative;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 75px;
    background-color: #fff;
    z-index: 999;
    position: sticky;
    top: 0;
    box-shadow: inset 0 -1px 0 0 #4f46e5;

}

.header__icon {
    font-size: 2rem;
    padding-right: 10px;
    color: #4f46e5;
}

.header__logo {
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    padding-left: 10px;
}

.header__logo img {
    width: 50px;
}

.header__logo h1 {
    font-size: 1.25rem;
}


.header__navb {
    position: absolute;
    bottom: 75px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    justify-items: stretch;
    width: 100%;
    padding: 20px 10px;
    background-color: #fff;
}

.header__nav .nav-itemu {
    padding: 10px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px solid #4f46e5;
    border-radius: 10px;
    text-align: center;
}

.header__nav .nav-itemu i {
    font-size: 1.25rem;
}

.header__nav .nav-itemu:hover {
    background-color: #4f46e5;
    cursor: pointer;
    color: #fff;
}

@media (min-width: 458px) {
    .header__nav {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 1024px) {
    .header {
        top: 0;
    }

    .header__icon {
        display: none;
    }

    .header__navb {
        clip-path: none !important;
        max-width: 768px;
        grid-template-columns: repeat(6, 1fr);
        position: relative;
        bottom: 0;
        justify-content: flex-end;
        gap: 0;
    }

    .header__nav .nav-itemu {
        padding: 0;
        border: none;
        flex-direction: row;
        gap: 5px;
    }

    .header__nav .nav-item:hover {
        background-color: transparent;
        color: #4f46e5;
        cursor: pointer;
    }
}